import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Form, Input } from 'antd';
import { typeOfTraining } from 'dump/data';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    code: '',
    name: '',
    nameEn: '',
    regulation: 0,
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'formSystemTraining' })
class FormSystemTrainingPage extends Component {
  state = {
    systemId: false,
    initialValue: {},
  };

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.setState({ systemId: match.params.id });
      this.getTeacherById(match.params.id);
    }
  };

  getTeacherById = code => {
    const systemMap = typeOfTraining.filter(system => system.code === code)[0];
    const initialValue = handleInitialValue(systemMap);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { systemId } = this.state;
    return (
      <Card title={systemId ? 'Cập nhật hệ đào tạo' : 'Thêm hệ đào tạo'}>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
            <Form.Item hasFeedback label="Mã hệ" {...formItemLayout}>
              {getFieldDecorator('code', {
                initialValue: initialValue.code,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Tên hệ" hasFeedback>
              {getFieldDecorator('name', {
                initialValue: initialValue.name,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Tên hệ Tiếng anh" hasFeedback>
              {getFieldDecorator('nameEn', {
                initialValue: initialValue.nameEn,
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Quy chế" hasFeedback>
              {getFieldDecorator('regulation', {
                initialValue: initialValue.regulation,
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel href="#system-training/list" />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormSystemTrainingPage {...props} />
  </Page>
));



